import React from "react";
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'
import Modal from 'react-bootstrap/Modal'
import JumboBooks from "../Components/Jumbotrons/JumboBooks";
import { useEffect, useState } from "react";

export default function Books({ action }) {
    const galleryLibrary = {
        book1: [
            {
                label: "Washington on the Parties c. 1800",
                fileName: "Washington"
            },
            {
                label: "Jackson and the Mother Bank 1850",
                fileName: "Jackson Mother Bank"
            },
            {
                label: "Nullification Crisis of 1832",
                fileName: "Despotism"
            },
            {
                label: "King Andrew the First 1833",
                fileName: "King Andrew the First"
            },
            {
                label: "Independent Treasury 1820",
                fileName: "Independent Treasury"
            },
            {
                label: "Whig President 1848",
                fileName: "Available Candidate"
            },
            {
                label: "Free Soil Banner 1848",
                fileName: "Free Soil Banner"
            },
            {
                label: "Citizen Know Nothing c. 1854",
                fileName: "Citizen Know Nothing"
            },
            {
                label: "Panic of 1857",
                fileName: "Panic 1857"
            },
            {
                label: "National Union Party 1864",
                fileName: "National Union Party"
            },
            {
                label: "King Andy 1866",
                fileName: "King Andy"
            },
            {
                label: "Erie Railroad Ring 1872",
                fileName: "Erie"
            },
            {
                label: "Boy of the Period 1869",
                fileName: "Boy of Period"
            },
            {
                label: "Liberal Republican Banner 1872",
                fileName: "Liberal Republican Banner"
            },
            {
                label: "Nassau Street Panic of 1873",
                fileName: "Panic of 1873 Sept 19"
            },
            {
                label: "Force of Example 1876",
                fileName: "Force of Example"
            },
            {
                label: "Probe Away 1876",
                fileName: "Probe Away"
            },
            {
                label: "Stride of a Century 1876",
                fileName: "Centennial"
            },
            {
                label: "I Feed You All 1875",
                fileName: "I Feed You All"
            },
            {
                label: "The Great Strike of 1877",
                fileName: "Great Strike"
            },
            {
                label: "New Ally 1887",
                fileName: "New Ally"
            },
            {
                label: "Chinese Exclusion Ad 1886",
                fileName: "George Dee China"
            },
            {
                label: "Knights of Labor Picnic 1882",
                fileName: "1st Annual Picnic"
            },
            {
                label: "Greenback Party Banner 1876",
                fileName: "Greenback Party"
            },
            {
                label: "Political Farce of 1876",
                fileName: "Political Farce of 1876"
            }
        ],
        book2: []
    }
    const [showBookModal, toggleBookModal] = useState(false);
    const [showGalleryModal, toggleGalleryModal] = useState(false);
    const [whichBook, toggleWhichBook] = useState(-1);
    const [whichIndex, toggleWhichIndex] = useState(-1);
    const [showMore, toggleShowMore] = useState(-1);
    const whichBookText = whichBook === 1 ? "book1" : "book2";
    const whichLibrary = galleryLibrary[whichBookText];


    useEffect(() => {
        if (action === "bookScroll")
            window.scrollTo({
                top: 1400,
                behavior: 'smooth'
            });
    });

    const RenderTOCImages = () => {
        const images = [];
        for (let i = 0; i < 8; i++) {
            images.push(
                <img
                    className="toc-image"
                    key={i}
                    src={`images/book1/toc${i}.png`}
                    alt={`tocImage`}
                />
            )
        }
        return images;
    }

    const RenderGallery = ({ book, extra }) => {
        const whichBookText = book === 1 ? "book1" : "book2"
        const whichLibrary = galleryLibrary[whichBookText];
        const cards = [];
        const low = extra ? 10 : 0;
        const high = extra ? 25 : 10;
        for (let i = low; i < high; i++) {
            cards.push(
                <Card
                    key={i}
                    id={i}
                    onClick={() => {
                        toggleWhichBook(book);
                        toggleWhichIndex(i);
                        toggleGalleryModal(true);
                    }}
                    className="bg-dark text-white galleryCard cursor mr-2 mt-4">
                    <Card.Img
                        src={`images/${whichBookText}/gallery/${whichLibrary[i].fileName}.jpg`}
                        alt={`${whichLibrary[i].label}`} />
                    <Card.ImgOverlay>
                        <Card.Text
                            className="mx-auto">
                            {whichLibrary[i].label}
                        </Card.Text>
                    </Card.ImgOverlay>
                </Card>
            )
        }
        return cards;
    };

    const ShowMoreGallery = ({ book }) => {
        return <RenderGallery
            book={book}
            extra={true}
        />;
    }

    return (
        <div>
            <JumboBooks />
            <Container className="container-body booksContainer" style={{ marginBottom: "50px" }}>
                <Row style={{ justifyContent: "center" }}>
                    <Col>
                        <h2 className="header-sub">THE ART OF POLITICAL FINANCE</h2>
                        <h2 className="header-sub2">Volume I - Part I</h2>
                        <h2 className="header-main textColorPrimary">THE COLONIAL ERA THROUGH RECONSTRUCTION</h2>
                        <img
                            className="cover-books"
                            src="images/book1/front1.jpg"
                            alt="cover"
                            height="450px"
                            width="310px" />
                        <img
                            className="cover-books"
                            src="images/book1/back.jpg"
                            alt="cover"
                            height="450px"
                            width="310px" />
                        <Button
                            onClick={() => toggleBookModal(true)}
                            className="d-block mx-auto mt-5 w-25"
                            variant="outline-dark">
                            Look Inside</Button>
                        <Button
                            className="d-block mx-auto mt-3 w-25 buyButton">Buy</Button>
                    </Col>
                </Row>
                <Row className="px-5">
                    <p
                        className="textBlock"
                        style={{ marginTop: "35px" }}><span className="dropcap">P</span>art I leverages over 60 illustrations by artists and publishers like Frank Leslie and his Illustrated Newspaper, Thomas Nast and Harper’s Weekly, Currier &amp; Ives, Joseph Keppler and Puck Magazine, and others to guide you through this often uninspected history. The images are a tribute to the extremely talented artists and their adroit perception of the issues. The book contains many accompanying summaries which in many cases are just as important as the surrounding text to help us understand the setting.</p>
                </Row>
                <RenderGallery book={1} />
                {showMore > -1 && <ShowMoreGallery book={1} />}
                {showMore < 0 &&
                    <i
                        onClick={() => toggleShowMore(1)}
                        className="fas fa-plus-circle d-block mt-2"></i>}
                <hr className="hr0" />
                <hr className="hr1" />
                <Row style={{ justifyContent: "center" }}>
                    <Col>
                        <h2 className="header-sub">THE ART OF POLITICAL FINANCE</h2>
                        <h2 className="header-sub2">Volume I - Part II</h2>
                        <h2 className="header-main textColorPrimary">THE GUILDED AGE</h2>
                        <img
                            className="cover-books"
                            src="images/book2/front1.jpg"
                            alt="cover"
                            height="450px"
                            width="310px" />
                        <img
                            className="cover-books"
                            src="images/book2/back.jpg"
                            alt="cover"
                            height="450px"
                            width="310px" />
                        <Button
                            className="d-block mx-auto mt-5 w-25" variant="outline-dark">Look Inside!</Button>
                        <Button
                            className="d-block mx-auto mt-3 w-25 buyButton">Buy</Button>
                    </Col>
                </Row>
                <Row className="px-5">
                    <p
                        className="textBlock" style={{ marginTop: "35px" }}><span className="dropcap">P</span>art II also looks at over 90 illustrations as political cartooning became more popular and more prevalent. In addition to the artists who were showcased in Part I, several more added to this Gilded Age rendition. Names like William A. Rogers, Bernhard and Victor Gillam, Frederick Opper, Grant Hamilton, Louis Dalrymple, Watson Heston, and others joined the list of artists as magazines like Judge and The Truth Seeker augmented the humor and illustrated publications of the period.</p>
                </Row>
                {/* <RenderGallery book={2} /> */}
                {/* <i className="fas fa-plus-circle d-block mt-2"></i> */}
            </Container>
            <Modal
                show={showBookModal}
                onHide={() => toggleBookModal(false)}
                size={"lg"}>
                <Modal.Body
                    style={{ paddingBottom: "500px" }}
                >
                    <RenderTOCImages />
                </Modal.Body>
            </Modal>
            <Modal
                show={showGalleryModal}
                onHide={() => toggleGalleryModal(false)}
                size={"lg"}>
                <Modal.Body
                    className="mx-auto">
                    {whichBook > -1 &&
                        <img className="galleryModalImg w-100" src={`images/${whichBookText}/gallery/${whichLibrary[whichIndex].fileName}.jpg`} />}
                </Modal.Body>
                {/* <Modal.Footer>
                    <Button variant="secondary" onClick={() => toggleGalleryModal(false)}>
                        Close
                    </Button>
                </Modal.Footer> */}
            </Modal>
        </div >
    )
}